import { getToken, setToken, removeToken } from '@/utils/auth'
import { commonLogin, getUserInfo } from '@/api/my'
import { setSession, getSession } from '@/utils/session'

/**
 * User STORE
 */

const store = {

  state: {
    userId: '',
    userAccount: '',
    userName: '',
    userStatus: '',
    avatar: '../static/myAvatar200.png', // static
    token: getToken(),
    roles: null, // ?
    warehouseList: [],
    projectList: [],
  },

  mutations: {
    SET_TOKEN (state, token) {
      if (token) {
        state.token = token
        setToken(token)
      }
    },
    REMOVE_TOKEN (state, data) {
      state.token = null
      removeToken()
    },
    SET_USERINFO (state, data) {
      state.userId = data.userId
      state.userAccount = data.userAccount
      state.userName = data.userRealName
      state.userStatus = data.userStatus
      state.roles = data.roles // static roles
      // state.avatar = data.avatar
      // state.roles = data.roles
    },
    RESET_USERINFO (state, data) {
      state.userId = ''
      state.userAccount = ''
      state.userName = ''
      state.userStatus = ''
      state.avatar = ''
      state.roles = null
    },
    SET_WAREHOUSE (state, data) {
      state.warehouseList = data
      setSession('warehouseUuid', data[0].warehouseUuid)
    },
    SET_PROJECT (state, data) {
      state.projectList = data
      setSession('projectId', data[0].projectId)
    },
  },

  actions: {
    /**
     * 
     * mutationToken cookie;
     *
     * @param {any} {commit}
     * @param {any} params
     * @returns
     */
    userLogin ({commit, state}, params) {
      return new Promise((resolve, reject) => {
        params.sysType='5'
        commonLogin(params)
          .then(res => {
            // TOKEN, TokenuserId
            commit('SET_TOKEN', res.token)
            resolve()
          }).catch(err => {
            reject(err)
          })
      })
    },
    /**
     * 
     * mutationsToken cookie,
     *
     * @param {any} {commit}
     * @returns
     */
    userLogout ({commit}) {
      return new Promise((resolve, reject) => {
        // 
        commit('REMOVE_TOKEN')
        // 
        commit('RESET_USERINFO')
        // tab,mutation store/views.js
        commit('REMOVE_ALL_VISITED')
        // , mutation  store/asyncRouter.js
        commit('RESET_ROUTERS')
        //
        resolve()
      })
    },
    /**
     * 
     * 
     *
     * @param {any} {commit}
     * @param {any} params
     * @returns
     */
    getUserInfo ({commit, state}, params) {
      return new Promise((resolve, reject) => {
        getUserInfo({'token': state.token})
          .then(res => {
            // TODO: , 
            // TODO: 
            res.roles = res.userAccount === 'admin' ? ['admin'] : ['user']
            commit('SET_USERINFO', res)
            resolve(res)
          }).catch(err => {
            reject(err)
          })
      })
    }
  }
}

export default store



